.common-menu-item{
    width: 100%;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    display: flex;
    flex-direction: column;
}

.common-menu-item .common-menu-item-row{
    position: relative;
    display: flex; flex-direction: row;  align-items: center; justify-content: center;
    gap: 0; padding: 0;
    font-size: 20px; line-height: 1em;
    cursor: pointer;
    height: 52px;
    border-radius: 2px;
    color: var(--second-color);
    width: 100%;
    background-color: rgba(255, 255, 255, 0.15);
}

.common-menu-item.zap-green .common-menu-item-row{
    color: #25D366!important;
}


.common-menu-item .common-menu-item-row:nth-child(1) > :nth-child(2){
    display: none;
}

/* Menu expanded */
.show .common-menu-item .common-menu-item-row{
    justify-content: flex-start;
    padding: 0 20px;
    gap: 10px; 
}

.show .common-menu-item .common-menu-item-row:nth-child(1) > :nth-child(2){
    display: inline;
    font-size: 16px;
}

.common-menu-item .common-menu-item-row:hover {
    transition: all .02s ease-in;
    background-color: rgba(144, 168, 236, 0.2);
}

.common-menu-item-row-body {
    display: none;
    height: fit-content;
    background-color: rgba(144, 168, 236, 0.3);
}

/*  body */

.common-menu-item-row-body.expand {
    display: flex;
    flex-direction: column;
}

.common-menu-item-row-body.expand > .common-menu-item .common-menu-item-row{
    height: 42px;
    font-size: 15px;
    background-color: rgba(232,232,232);
    color:#333;
}

.common-menu-item-row-body.expand > .common-menu-item .common-menu-item-row:hover {
    transition: all .02s ease-in;
    background-color: rgba(144, 168, 236, 0.2);
}